<template>
  <div class="grid">
    <div class="col-12">
      <div class="card">
        <h5>DataView</h5>
        <DataView
          :value="dataviewValue"
          :layout="layout"
          :rows="5"
          :sortOrder="sortOrder"
          :sortField="sortField"
        >
          <template #grid="slotProps">
            <div class="col-12 md:col-2">
              <div class="card">
                <div class="text-center overflow-scroll">
                  <div class="text-2xl font-bold">{{ slotProps.data.clean }}</div>
                  <div class="mb-3">{{ slotProps.data.english }}</div>
                </div>
              </div>
            </div>
          </template>
        </DataView>
      </div>
    </div>
  </div>
</template>

<script>
import EventBus from "../AppEventBus";
import BibleService from "../service/BibleService";
export default {
  data() {
    return {
      dataviewValue: null,
      layout: "grid",
      sortKey: null,
      sortOrder: null,
      sortField: null,
      sortOptions: [
        { label: "Price High to Low", value: "!price" },
        { label: "Price Low to High", value: "price" },
      ],
    };
  },
  bibleService: null,
  created() {
    this.bibleService = new BibleService();
  },
  mounted() {
    this.getBibleData = (event) => {
      console.log("getBibleData", event);
      const that = this;
      this.bibleService
        .getBible(event.book.code, event.chapter.code, event.verse.code)
        .then(function (resp) {
          console.log(resp.data);
          for (var i = 0; i < resp.data.length; i++) {
            let strongs = resp.data[i].strongs;
            strongs = strongs.replace("God_§God@Gen.1.1", "God");
            strongs = strongs.replace("obj.", "the");
            let stuff = strongs.split("/");
            let english = "";
            for (var j = 0; j < stuff.length; j++) {
              let words = stuff[j].split("=");
              english += words[2].replace(/[§_]+/g, "").replace(/[0-9]/g, " ") + " ";
            }

            resp.data[i].english = english.replace("verseEnd", "").trim();
          }
          that.dataviewValue = resp.data;
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          // always executed
        });
    };
    EventBus.on("loadBible", (data) => {
      console.log("loadBible", data);
      this.getBibleData(data);
    });
  },

  methods: {
    onSortChange(event) {
      const value = event.value.value;
      const sortValue = event.value;

      if (value.indexOf("!") === 0) {
        this.sortOrder = -1;
        this.sortField = value.substring(1, value.length);
        this.sortKey = sortValue;
      } else {
        this.sortOrder = 1;
        this.sortField = value;
        this.sortKey = sortValue;
      }
    },
  },
};
</script>

